/* =====================
        Colors
=======================*/
$dark: rgb(46, 40, 48);
$blue: rgba(0, 110, 255, 0.849);

/* ==================
    Break Points 
====================*/
$bp-small: 48em; //530px 33em

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
    text-decoration: none;
    list-style: none;
}

html {
    box-sizing: border-box;
    font-size: 62.5%; //10px/16px = 62.5%, 1rem = 10px;
}

body {
    background-color: rgb(46, 40, 48);
}